@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-size: Noto Sans TC, Microsoft JhengHei, Helvetica Neue, Arial, sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
img {
  width: 100%;
  max-width: 100%;
  border: 0;
}

/* 首頁 games 輪播 */
.swiper-slide {
  opacity: 0;
  transition: all 0.3s ease 0s;
}
.swiper-slide.swiper-slide-active, .swiper-slide.swiper-slide-next {
  opacity: 1;
  transition: all 0.3s ease 0s;
}

/* 箭頭 */
.iGameBox .swiper-button-prev, .iGameBox .swiper-button-next {
  display: block;
  width: 60px;
  height: 60px;
  top: 60%;
  background-size: 100%;
  background-image: url(../public/images/bg/ban_aw_r.png);
  background-repeat: no-repeat;
}
.iGameBox .swiper-button-prev:after, .iGameBox .swiper-button-next:after {
  content: none;
}
.iGameBox .swiper-button-prev {
  background-image: url(../public/images/bg/ban_aw_l.png);
}
.iGameBox .swiper-button-next {
  /* right: -20px !important; */
  /* top: 50%; */
  /* transform: translate(0, -50%); */
  /* margin: -10px 0 0 */
}

/* 點點 */
.swiper-pagination {
  display: none;
}
.swiper-pagination .owl-dot.swiper-pagination-bullet {
  margin: 0 7px;
  display: inline-block;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;

  background-color: #beb9b3;
  width: auto;
  height: auto;
  opacity: 1;
}
.swiper-pagination .owl-dot.swiper-pagination-bullet span {
  width: 28px;
  height: 8px;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  display: block;
}
.owl-controls2 .owl-dot.active,
.owl-controls2 .owl-dot:hover,
.swiper-pagination .owl-dot.swiper-pagination-bullet.swiper-pagination-bullet-active,
.swiper-pagination .owl-dot.swiper-pagination-bullet:hover {
  background-color: #fccf00;
}

@media only screen and (max-width: 768px) {
  .swiper-pagination {
    display: block;
  }
  .iGameBox .swiper-button-prev, .iGameBox .swiper-button-next {
    display: none;
  }
}

/* openYearList */
.yearMenu.active > .openYearBtn {
  color: #7B7369;
}
.yearMenu.active > .openYearList {
  display: block;
  height: auto;
}

.forumSidebar.open {
  transform: translate(0, 0);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 0px 15px;
}

/* 列表短敘述限制行數 */
.desc {
  display: -webkit-box;
}

/* 404、about banner */
section.notFound, .aboutBanWrap {
  background-image: url(../public/images/bg/p_about_ban_top_bg.png), url(../public/images/bg/p_about_ban_bot_bg.png);
  background-position: top center, bottom center;
  background-repeat: repeat-x;
}
